import { supabase } from './supabaseClient';
import { Client, ClientContact, CreateClientData, UpdateClientData, ClientStats, ClientListParams, ClientListResponse } from '../types/client';

export const clientService = {
  async getClients(params: ClientListParams): Promise<ClientListResponse> {
    let query = supabase.from('clients').select('*', { count: 'exact' });

    // Apply filters
    if (params.status) {
      query = query.eq('status', params.status);
    }
    if (params.search) {
      query = query.ilike('company_name', `%${params.search}%`);
    }

    // Apply sorting
    if (params.sortBy) {
      query = query.order(params.sortBy, { ascending: params.sortOrder === 'ASC' });
    }

    // Apply pagination
    const page = params.page || 1;
    const limit = params.limit || 10;
    const { data, error, count } = await query.range((page - 1) * limit, page * limit - 1);

    if (error) {
      console.error('Error fetching clients:', error);
      throw error;
    }
    
    const totalClients = count || 0;
    const totalPages = Math.ceil(totalClients / limit);

    return {
      clients: data || [],
      pagination: {
        currentPage: page,
        totalPages,
        totalClients,
        hasNext: page < totalPages,
        hasPrev: page > 1,
        limit: limit,
      }
    };
  },

  async getClientById(id: string): Promise<Client | null> {
    const { data, error } = await supabase.from('clients').select('*').eq('id', id).single();
    if (error) {
      console.error(`Error fetching client with id ${id}:`, error);
      throw error;
    }
    return data;
  },

  async createClient(clientData: CreateClientData): Promise<Client> {
    const { data, error } = await supabase.from('clients').insert([clientData]).single();
    if (error) {
      console.error('Error creating client:', error);
      throw error;
    }
    return data;
  },

  async updateClient(id: string, updates: UpdateClientData): Promise<Client> {
    const { data, error } = await supabase.from('clients').update(updates).eq('id', id).single();
    if (error) {
      console.error(`Error updating client with id ${id}:`, error);
      throw error;
    }
    return data;
  },

  async deleteClient(id: string): Promise<void> {
    const { error } = await supabase.from('clients').delete().eq('id', id);
    if (error) {
      console.error(`Error deleting client with id ${id}:`, error);
      throw error;
    }
  },

  async getClientContacts(clientId: string): Promise<ClientContact[]> {
    const { data, error } = await supabase.from('client_contacts').select('*').eq('client_id', clientId);
    if (error) {
      console.error(`Error fetching contacts for client ${clientId}:`, error);
      throw error;
    }
    return data || [];
  },

  async addClientContact(clientId: string, contactData: Omit<ClientContact, 'id' | 'clientId'>): Promise<ClientContact> {
    const { data, error } = await supabase.from('client_contacts').insert([{ ...contactData, client_id: clientId }]).single();
    if (error) {
      console.error('Error adding client contact:', error);
      throw error;
    }
    return data;
  },

  async updateClientContact(clientId: string, contactId: string, contactData: Partial<ClientContact>): Promise<ClientContact> {
    const { data, error } = await supabase
      .from('client_contacts')
      .update(contactData)
      .eq('client_id', clientId)
      .eq('id', contactId)
      .single();
    if (error) {
      console.error('Error updating client contact:', error);
      throw error;
    }
    return data;
  },

  async deleteClientContact(clientId: string, contactId: string): Promise<void> {
    const { error } = await supabase
      .from('client_contacts')
      .delete()
      .eq('client_id', clientId)
      .eq('id', contactId);
    if (error) {
      console.error('Error deleting client contact:', error);
      throw error;
    }
  },

  getStatusColor(status: string): string {
    switch (status) {
      case 'lead':
        return 'bg-blue-100 text-blue-800';
      case 'opportunity':
        return 'bg-yellow-100 text-yellow-800';
      case 'active':
        return 'bg-green-100 text-green-800';
      case 'inactive':
        return 'bg-gray-100 text-gray-800';
      case 'lost':
        return 'bg-red-100 text-red-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  },

  async getClientStats(): Promise<ClientStats> {
    const { data, error, count } = await supabase
      .from('clients')
      .select('status', { count: 'exact' });

    if (error) {
      console.error('Error fetching client stats:', error);
      throw error;
    }

    const statusCounts = data.reduce((acc: { [key: string]: number }, { status }: { status: string }) => {
      acc[status] = (acc[status] || 0) + 1;
      return acc;
    }, {});

    return {
      totalClients: count || 0,
      activeClients: statusCounts['active'] || 0,
      leads: statusCounts['lead'] || 0,
      opportunities: statusCounts['opportunity'] || 0,
      inactiveClients: statusCounts['inactive'] || 0,
      lostClients: statusCounts['lost'] || 0,
      overdueFollowUps: 0,
      conversionRate: 0,
      averageRating: 0,
      totalEstimatedValue: 0,
      clientsBySource: [],
      clientsBySize: [],
      clientsByStatus: [],
      clientsByLocation: [],
      monthlyConversions: [],
      recentActivities: [],
    };
  },

  async updateClientStatus(id: string, status: string): Promise<Client> {
    const { data, error } = await supabase
      .from('clients')
      .update({ status })
      .eq('id', id)
      .single();

    if (error) {
      console.error(`Error updating client status for id ${id}:`, error);
      throw error;
    }
    return data;
  },

  formatCurrency(amount: number): string {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
    }).format(amount);
  },
};
